import { Injectable } from "@angular/core";
import { HttpClient, HttpErrorResponse } from '@angular/common/http';
import { NgForm } from '@angular/forms';
import { Observable,throwError } from 'rxjs';
import { httpOptions } from "../../../../globalmodel/headerDetails";
import { catchError, retry } from 'rxjs/operators';
import {url} from '../../../../globalmodel/url';
import { SearchObject } from '../model/searchObject';
import { SubProduct } from 'src/app/globalmodel/listitem/subProduct';

@Injectable()

export class searchList{
  url= url.localRegisteruri;
  message:string= ''
    constructor(private http:HttpClient){  }
 handleError(error:HttpErrorResponse){
  if(error.error instanceof ErrorEvent){
   this.message='Front End Error'
    } else {
    this.message='Backend Error'
    }
      return throwError(
  this.message)
     };

   

    search(searchObject:SearchObject):Observable<SubProduct[]>{
    
      return this.http.post<SubProduct[]>(this.url,searchObject,httpOptions.jsonHeaders).pipe(
      catchError(this.handleError));    
    }


}